<!--  -->
<template>
  <td
    :class="{'boom':cellData.isShowBoom, 'mine-clear': cellData.isClear ,'marked':cellData.isMarked}"
    @mousedown="cellClick"
    class="cell"
    :style="{ width: size.w + 'px', height: size.h + 'px' }"
  >
    <span
      :style="{color:getNumColor(cellData.data)}"
      v-if="cellData.data != 0 && cellData.isClear"
    >{{ cellData.data }}</span>
  </td>
</template>
<script>
import EventBus from "../eventBus.js";
export default {
  //import引入的组件需要注入到对象中才能使用
  name: "cell",
  components: {},
  data() {
    //这里存放数据
    return {};
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    getNumColor(num) {
      switch (num) {
        case 1:
          return "#fff";
        case 2:
          return "#c6a";
        case 3:
          return "#08a";
        case 4:
          return "#0a2";
        case 5:
          return "#a20";
        case 6:
          return "#f30";
        default:
          return "#f90";
      }
    },
    cellClick(e) {
      if (this.cellData.isClear) {
        return;
      }

      if (e.button === 2) {
        EventBus.$emit("click-cell");
        this.$set(this.cellData, "isMarked", !this.cellData.isMarked);
        return;
      }
      if (e.button === 0) {
        if (this.cellData.isBoom) {
          EventBus.$emit("boom-end");
          console.log("boom");
        } else {
          // this.$set(this.cellData, "isClear", true);
          if (this.cellData.isMarked) {
            return;
          }
          EventBus.$emit("click-cell");
          this.$emit("clearboom", this.cellData.cellIndex);
        }
      }
    }
  },
  props: {
    cellData: {
      type: Object,
      required: true
    },
    size: {
      type: Object,
      required: false,
      default: function() {
        return {
          w: 30,
          h: 30
        };
      }
    }
  }
};
</script>

<style lang="scss" scoped>
//@import url(); 引入公共css类
td {
  text-align: center;
  vertical-align: middle;
}
.mine-clear {
  background-color: #333 !important;
}
.marked {
  background-image: url(/flag.png) !important;
  background-size: cover;
}
.boom {
  position: relative;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(/zd.png) !important;
    background-size: cover;
  }
}
</style>
